<template>
  <div class="hello">
    <h2>测试和 springboot 项目配合使用接口</h2>
    <div class="input-box">
      <p>name：</p>
      <input v-model="userInfo.name" />
      <br>
      <p>age：</p>
      <input v-model="userInfo.age" />
      <br>
      <p>grade：</p>
      <input v-model="userInfo.grade" />
    </div>
    <button @click="testPostInsertRecord">插入数据</button>

    <table cellspacing="10">
      <tr>
        <th>id</th>
        <th>name</th>
        <th>age</th>
        <th>grade</th>
      </tr>
      <tr v-for="record in allRecords" :key="record.id">
        <td>{{ record.id }}</td>
        <td>{{ record.name }}</td>
        <td>{{ record.age }}</td>
        <td>{{ record.grade }}</td>
      </tr>
    </table>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: 'HelloWorld',
  data() {
    return {
      baseUrl: "http://localhost:8080",
      allRecords: [],
      userInfo: {
        name: "",
        age: null,
        grade: null
      }
    }
  },
  mounted() {
    this.testGetAllRecords();
  },
  methods: {
    /**
     * 访问测试接口
     * 成功返回 HELLO
     */
    testHello() {
      const url = this.baseUrl + "/hello";
      axios.get(url).then(res => {
        console.log('res.data', res.data);
      }).catch(err => {
        console.log('err', err);
      })
    },
    /**
     * 访问"查询所有信息"接口
     * 成功返回 所有信息json
     */
    testGetAllRecords() {
      const url = this.baseUrl + "/findAll";
      axios.get(url).then(res => {
        console.log('res.data', res.data);

        this.allRecords = res.data;
      }).catch(err => {
        console.log('err', err);
      })
    },
    /**
     * 访问"插入数据"接口
     * 成功返回 “插入数据成功！”
     */
    testPostInsertRecord() {
      const url = this.baseUrl + "/insertRecord";
      axios({
        url: url,
        method: 'post',
        "content-type": "application/x-www-form-urlencoded",
        data: this.userInfo
      }).then((res) => {
        console.log("res.data:", res.data);
        // 插入成功后刷新数据
        this.testGetAllRecords();
      }).catch((err) => {
        console.log("err:", err);
      });
    },
  }
}
</script>

<style scoped>
.input-box p {
  display: inline-block;
  width: 60px;
}

table {
  background-color: aqua;
  margin: 20px auto;

}
</style>